html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  font-size: 18px;
  line-height: 1.5;
  color: #f5f5f5;

  background-color: #05e8ba;
  background-image: linear-gradient(315deg, #05e8ba 0%, #087ee1 74%);
  @extend %s400;
}

.wrapper {
  margin: 0 auto;
  max-width: 1620px;
}

.main {
  margin-top: 6px;
  transform: translateY(18px);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.container-default {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1650px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  font-size: 48px;
  white-space: nowrap;
  text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.2),
    1px 1px 1px rgba(0, 0, 0, 0.6);
  color: $title;
  transition: transform 0.6s;

  @include mq("600") {
    font-size: 32px;
  }

  @extend %l500;
}

.hover-right {
  & .split--right {
    width: 75%;

    @include mq("1100") {
      width: 50%;
    }

    @include mq("600") {
      width: 90%;
    }
  }

  & .split--left {
    width: 25%;

    @include mq("1100") {
      width: 50%;
    }

    @include mq("600") {
      overflow: hidden;
      width: 10%;

      .title.content__title {
        margin-top: 160px;
        transform: rotate(-90deg);
      }

      .cards {
        transform: translateX(-100vh);
      }
    }
  }
}

.hover-left {
  & .split--right {
    width: 25%;

    @include mq("1100") {
      width: 50%;
    }

    @include mq("600") {
      overflow: hidden;
      width: 10%;

      .title.contacts__title {
        margin-top: 160px;
        transform: rotate(-90deg);
      }

      .contacts__inner {
        transform: translateX(100vh);
      }
    }
  }

  & .split--left {
    width: 75%;

    @include mq("1100") {
      width: 50%;
    }

    @include mq("600") {
      width: 90%;
    }
  }

  & .contacts__inner {
    flex-wrap: wrap;
    justify-content: center;
  }

  & .contacts__body {
    margin: 0 0 30px;
    min-width: 100%;
  }

  & .contacts__content {
    animation-name: "op";
    animation-duration: 0.8s;
    animation-timing-function: ease;
  }
}

.portfolio {
  display: flex;
  min-width: 100%;
  min-height: 100vh;
}

.split {
  overflow-x: hidden;
  overflow-y: scroll;
  width: 49%;
  min-height: 800px;
  max-height: 100vh;
  margin: 0 0.5%;
  padding: 0;

  &.split--left {
    background-image: linear-gradient(to bottom,
        rgba(228, 249, 245, 0.45),
        rgba(213, 246, 242, 0.45),
        rgba(197, 242, 241, 0.45),
        rgba(181, 239, 241, 0.45),
        rgba(164, 235, 243, 0.45));

    border-radius: 6px;
    box-shadow: rgba(137, 196, 244, 0.4) -5px 5px, rgba(137, 196, 244, 0.3) -10px 10px, rgba(137, 196, 244, 0.2) -15px 15px, rgba(137, 196, 244, 0.1) -20px 20px, rgba(137, 196, 244, 0.05) -25px 25px;    border: 1px solid rgba(0, 0, 0, 0.09);
  }

  &.split--right {
    background-image: linear-gradient(to right top,
        rgba(48, 227, 202, 0.75),
        rgba(0, 195, 190, 0.75),
        rgba(0, 163, 172, 0.75),
        rgba(26, 132, 148, 0.75),
        rgba(39, 102, 120, 0.75));
  }

  border-radius: 6px;
  box-shadow: rgba(0, 105, 146, 0.4) 5px 5px, rgba(0, 105, 146, 0.3) 10px 10px, rgba(0, 105, 146, 0.2) 15px 15px, rgba(0, 105, 146, 0.1) 20px 20px, rgba(0, 105, 146, 0.05) 25px 25px;  border: 1px solid rgba(0, 0, 0, 0.09);

  &.split--left,
  &.split--right {
    transition: width 0.6s ease-in-out;
  }
}

.split::-webkit-scrollbar,
.cards::-webkit-scrollbar {
  width: 0.3em;
  height: 1em;
}

.split::-webkit-scrollbar-track,
.cards::-webkit-scrollbar-track {
  background: hsl (120 75% 50% / 1);
  border-radius: 100vw;
  margin-block: 0.5em;
}

.split::-webkit-scrollbar-thumb,
.cards::-webkit-scrollbar-thumb {
  background: hsl (120 100% 20% / 1);
  border: 0.25em solid hsl (120 75% 50% / 1);
  border-radius: 100vw;
}

.split::-webkit-scrollbar-thumb:hover,
.cards::-webkit-scrollbar-thumb:hover {
  background: hsl (120 100% 10% / 1);
}

.lang-changer {
  position: relative;
  font-size: 24px;

  &__value {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
  }
}

.logo {
  display: flex;
  align-items: center;
  font-size: 24px;
  padding: 4px;

  &__link {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    color: #000;
  }

  &__icon {
    margin-right: 6px;
    width: 50px;
    height: 50px;
  }

  &__subtitle {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 34px;
    font-size: 14px;
    font-style: italic;
    text-shadow: 1px 1px 1px #000;

    &--animated {
      animation-name: "up";
      animation-duration: 1.5s;
      animation-timing-function: ease;
      animation-iteration-count: infinite;
    }
  }

  span {
    position: absolute;
    top: -6px;
    left: 4px;
    font-size: 20px;
  }
}