@charset "UTF-8";
@import url(../../node_modules/normalize.css/normalize.css);
html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
address,
img,
small,
strong,
sub,
sup,
b,
i,
ol,
ul,
li,
form,
label,
article,
aside,
footer,
header,
nav,
section,
time,
audio,
video {
  margin: 0;
  outline: 0;
  border: 0;
  padding: 0;
  background: transparent;
}

html {
  word-break: normal;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  scroll-behavior: smooth;
}

ul[class],
ol {
  list-style: none;
}

a {
  outline: 0;
  text-decoration: none;
  background: transparent;
  text-decoration-skip-ink: none;
}

address {
  font-style: normal;
}

input,
button,
textarea,
select {
  vertical-align: middle;
  margin: 0;
  border: none;
  border-radius: 0;
  padding: 0;
  font: inherit;
  color: inherit;
  background: transparent;
}

img {
  display: block;
  max-width: 100%;
}

:focus {
  outline: none;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
}

.title {
  font-family: "Lora", sans-serif;
  font-weight: 500;
}

/* source-sans-pro-regular - latin_cyrillic */
@font-face {
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* lora-500 - cyrillic */
@font-face {
  font-family: "Lora";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/lora-v17-cyrillic-500.woff2") format("woff2"), url("../fonts/lora-v17-cyrillic-500.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  font-size: 18px;
  line-height: 1.5;
  color: #f5f5f5;
  background-color: #05e8ba;
  background-image: linear-gradient(315deg, #05e8ba 0%, #087ee1 74%);
}

.wrapper {
  margin: 0 auto;
  max-width: 1620px;
}

.main {
  margin-top: 6px;
  transform: translateY(18px);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.container-default {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1650px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  font-size: 48px;
  white-space: nowrap;
  text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.2), 1px 1px 1px rgba(0, 0, 0, 0.6);
  color: #073b4c;
  transition: transform 0.6s;
}

@media only screen and (max-width: 599px) {
  .title {
    font-size: 32px;
  }
}

.hover-right .split--right {
  width: 75%;
}

@media only screen and (max-width: 1099px) {
  .hover-right .split--right {
    width: 50%;
  }
}

@media only screen and (max-width: 599px) {
  .hover-right .split--right {
    width: 90%;
  }
}

.hover-right .split--left {
  width: 25%;
}

@media only screen and (max-width: 1099px) {
  .hover-right .split--left {
    width: 50%;
  }
}

@media only screen and (max-width: 599px) {
  .hover-right .split--left {
    overflow: hidden;
    width: 10%;
  }
  .hover-right .split--left .title.content__title {
    margin-top: 160px;
    transform: rotate(-90deg);
  }
  .hover-right .split--left .cards {
    transform: translateX(-100vh);
  }
}

.hover-left .split--right {
  width: 25%;
}

@media only screen and (max-width: 1099px) {
  .hover-left .split--right {
    width: 50%;
  }
}

@media only screen and (max-width: 599px) {
  .hover-left .split--right {
    overflow: hidden;
    width: 10%;
  }
  .hover-left .split--right .title.contacts__title {
    margin-top: 160px;
    transform: rotate(-90deg);
  }
  .hover-left .split--right .contacts__inner {
    transform: translateX(100vh);
  }
}

.hover-left .split--left {
  width: 75%;
}

@media only screen and (max-width: 1099px) {
  .hover-left .split--left {
    width: 50%;
  }
}

@media only screen and (max-width: 599px) {
  .hover-left .split--left {
    width: 90%;
  }
}

.hover-left .contacts__inner {
  flex-wrap: wrap;
  justify-content: center;
}

.hover-left .contacts__body {
  margin: 0 0 30px;
  min-width: 100%;
}

.hover-left .contacts__content {
  animation-name: "op";
  animation-duration: 0.8s;
  animation-timing-function: ease;
}

.portfolio {
  display: flex;
  min-width: 100%;
  min-height: 100vh;
}

.split {
  overflow-x: hidden;
  overflow-y: scroll;
  width: 49%;
  min-height: 800px;
  max-height: 100vh;
  margin: 0 0.5%;
  padding: 0;
  border-radius: 6px;
  box-shadow: rgba(0, 105, 146, 0.4) 5px 5px, rgba(0, 105, 146, 0.3) 10px 10px, rgba(0, 105, 146, 0.2) 15px 15px, rgba(0, 105, 146, 0.1) 20px 20px, rgba(0, 105, 146, 0.05) 25px 25px;
  border: 1px solid rgba(0, 0, 0, 0.09);
}

.split.split--left {
  background-image: linear-gradient(to bottom, rgba(228, 249, 245, 0.45), rgba(213, 246, 242, 0.45), rgba(197, 242, 241, 0.45), rgba(181, 239, 241, 0.45), rgba(164, 235, 243, 0.45));
  border-radius: 6px;
  box-shadow: rgba(137, 196, 244, 0.4) -5px 5px, rgba(137, 196, 244, 0.3) -10px 10px, rgba(137, 196, 244, 0.2) -15px 15px, rgba(137, 196, 244, 0.1) -20px 20px, rgba(137, 196, 244, 0.05) -25px 25px;
  border: 1px solid rgba(0, 0, 0, 0.09);
}

.split.split--right {
  background-image: linear-gradient(to right top, rgba(48, 227, 202, 0.75), rgba(0, 195, 190, 0.75), rgba(0, 163, 172, 0.75), rgba(26, 132, 148, 0.75), rgba(39, 102, 120, 0.75));
}

.split.split--left, .split.split--right {
  transition: width 0.6s ease-in-out;
}

.split::-webkit-scrollbar,
.cards::-webkit-scrollbar {
  width: 0.3em;
  height: 1em;
}

.split::-webkit-scrollbar-track,
.cards::-webkit-scrollbar-track {
  background: hsl 120 75% 50%/1;
  border-radius: 100vw;
  margin-block: 0.5em;
}

.split::-webkit-scrollbar-thumb,
.cards::-webkit-scrollbar-thumb {
  background: hsl 120 100% 20%/1;
  border: 0.25em solid hsl 120 75% 50%/1;
  border-radius: 100vw;
}

.split::-webkit-scrollbar-thumb:hover,
.cards::-webkit-scrollbar-thumb:hover {
  background: hsl 120 100% 10%/1;
}

.lang-changer {
  position: relative;
  font-size: 24px;
}

.lang-changer__value {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
}

.logo {
  display: flex;
  align-items: center;
  font-size: 24px;
  padding: 4px;
}

.logo__link {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  color: #000;
}

.logo__icon {
  margin-right: 6px;
  width: 50px;
  height: 50px;
}

.logo__subtitle {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 34px;
  font-size: 14px;
  font-style: italic;
  text-shadow: 1px 1px 1px #000;
}

.logo__subtitle--animated {
  animation-name: "up";
  animation-duration: 1.5s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}

.logo span {
  position: absolute;
  top: -6px;
  left: 4px;
  font-size: 20px;
}

@keyframes op2 {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 0;
  }
}

.header {
  z-index: 3;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  transition: transform 0.3s linear;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 0 0 8px 8px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: #000;
}

.header.hide {
  transform: translateY(-160px);
}

.header.color .header__inner {
  box-shadow: 0px 10px 8px 0px rgba(34, 60, 80, 0.4);
  border-top: 0px;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.picker__form {
  position: relative;
}

.picker__form::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  display: block;
  /* Рисуем треугольник */
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10.4px 6px 0 6px;
  border-color: #7e9bbd transparent transparent transparent;
  /* Снимаем клик с треугольника */
  pointer-events: none;
}

.picker__label {
  position: relative;
}

.picker__icon {
  position: absolute;
  top: -3px;
  left: 6px;
  width: 26px;
  height: 26px;
}

.picker__select {
  /* Убираем браузерную стрелку */
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  /* Задаем свой фон и рамку */
  border: 1px solid #7E9BBD;
  border-radius: 6px;
  background-color: white;
  /* Отступы от рамки к тексту */
  padding: 6px 25px 6px 35px;
  /* Убираем баузерную обводку*/
  outline: none;
  cursor: pointer;
}

.picker::focus {
  outline: none;
  box-shadow: 0px 0px 0px 1px #38577a, 0px 0px 10px #7e9bbd;
}

.picker__flag {
  background-color: #fff;
}

.picker__flag--ukrain {
  background-image: url(/images/ukr.svg);
}

.picker__flag--english {
  background-image: url(/images/uk.svg);
}

.picker__flag--russian {
  background-image: url(/images/ru.svg);
}

.contacts__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.6s;
  width: 100%;
}

@media only screen and (max-width: 1399px) {
  .contacts__inner {
    flex-wrap: wrap;
  }
}

.contacts__body {
  margin-right: 20px;
}

@media only screen and (max-width: 1399px) {
  .contacts__body {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.contacts__subtitle {
  margin-bottom: 20px;
  text-align: center;
  font-size: 24px;
  color: #073b4c;
}

.contacts__content {
  margin: 0 10px 30px;
  border-radius: 6px;
  padding: 10px;
  min-width: 280px;
  font-size: 15px;
  color: #000;
  background: rgba(0, 0, 0, 0.05);
  backdrop-filter: blur(7.2px);
  border: 1px solid rgba(0, 0, 0, 0.09);
  user-select: none;
}

@media only screen and (max-width: 1099px) {
  .contacts__content {
    max-width: 100%;
    background: transparent;
    border: none;
    padding: 0;
    backdrop-filter: unset;
  }
}

.contacts__text {
  margin: 0 10px;
}

.contacts__top-title {
  margin-bottom: 20px;
  font-size: 18px;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
  text-align: center;
}

.contacts__middle-title {
  margin-bottom: 10px;
  font-size: 20px;
}

.contacts__middle-list {
  list-style: inside !important;
  margin-bottom: 15px;
}

.contacts__middle-list li {
  margin-bottom: 6px;
  font-size: 18px;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
}

.contacts__list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

@media only screen and (max-width: 1099px) {
  .contacts__list {
    margin-bottom: 30px;
  }
}

.contacts__item {
  transition: transform 0.4s;
  cursor: pointer;
}

.contacts__item:not(:last-child) {
  margin-right: 15px;
}

.contacts__item:hover {
  transform: scale(1.08);
}

.contacts__link {
  font-size: 1rem;
  color: #ececec;
}

.contacts__icon {
  max-width: 50px;
  max-height: 50px;
}

@media only screen and (max-width: 599px) {
  .contacts__icon {
    width: 40px;
  }
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 90vw;
  max-height: 1600px;
  transition: transform 0.6s;
}

.cards__item {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  flex: 0.5;
  margin: 10px;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  min-width: 250px;
  min-height: 300px;
  max-width: 500px;
  color: #fff;
  transition: flex 0.7s ease-in;
  cursor: pointer;
}

@media only screen and (max-width: 1099px) {
  .cards__item {
    flex: 0;
  }
}

@media only screen and (max-width: 599px) {
  .cards__item {
    min-width: 75vw;
  }
}

.cards__item.cards__item--active {
  flex: 5;
}

@media only screen and (max-width: 1099px) {
  .cards__item.cards__item--active {
    flex: 0;
  }
}

.cards__item.cards__item--active .cards__link {
  visibility: visible;
}

.cards__title {
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 20px;
  border-radius: 5px;
  padding: 5px 7px;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.65);
  opacity: 1;
}

.cards__link {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  visibility: hidden;
  cursor: pointer;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 2px solid #073b4c;
  margin: 0 auto 30px;
  color: #073b4c;
}

.info__title {
  font-size: 28px;
  text-align: center;
}

.info__img-wrapper {
  margin-bottom: 20px;
  max-width: 250px;
  max-height: 500px;
}

.info__img {
  border-radius: 6px;
  object-fit: cover;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
}

.footer {
  padding: 1rem 0;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 8px 8px 0 0;
}

.footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #000;
}
