.picker {
    &__form {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: 10px;
            transform: translateY(-50%);
            display: block;
            /* Рисуем треугольник */
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 10.4px 6px 0 6px;
            border-color: #7e9bbd transparent transparent transparent;
            /* Снимаем клик с треугольника */
            pointer-events: none;
        }
    }

    &__label {
        position: relative;
    }

    &__icon {
        position: absolute;
        top: -3px;
        left: 6px;
        width: 26px;
        height: 26px;
    }

    &__select {
        /* Убираем браузерную стрелку */
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;

        /* Задаем свой фон и рамку */
        border: 1px solid #7E9BBD;
        border-radius: 6px;
        // background-color: #fff;
        background-color: rgba($color: #fff, $alpha: 1.0);

        /* Отступы от рамки к тексту */
        padding: 6px 25px 6px 35px;

        /* Убираем баузерную обводку*/
        outline: none;

        cursor: pointer;
    }

    &::focus {
        outline: none;
        box-shadow: 0px 0px 0px 1px #38577a, 0px 0px 10px #7e9bbd;
    }

    &__flag {
        background-color: #fff;
    }

    &__flag--ukrain {
        background-image: url(/images/ukr.svg);
    }

    &__flag--english {
        background-image: url(/images/uk.svg);
    }

    &__flag--russian {
        background-image: url(/images/ru.svg);
    }
}